import Vue from 'vue';

import Component from 'vue-class-component';
import { State, Action } from 'vuex-class';

import { ITask } from '~/entity/task';
import { IlocalPage, Ipage } from '~/entity/page';
import { LOADING_TEXT, USER_ROLES } from '~/config';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';
import Utils from '~/utils';
import { IRecommendParams, IReceivedTaskDetailParams, IPublishedTaskParams, IPayParams } from '~/entity/route_params';
import { CACHE_NAME } from '~/cache';

/*
 * 输出红包领取情况模板： 已领取2/4个，共12.00/100.00元
 */
function redPacketAmount(task: ITask) {
    let receivedCount = 0;
		const totalCount = task.totalCount;
		let totalAmount: any = task.totalAmount / 100;
		let receivedAmount: any = 0;
		// 遍历子任务 查询被领取的子任务情况
		const subsetTasks = task.subsetTasks;
		for (const subsetTask of subsetTasks) {
			if (subsetTask.receiveStatus === 1) {
				receivedCount++;
				receivedAmount = subsetTask.amount / 100;
			}
		}
		receivedAmount = receivedAmount.toFixed(2);
		totalAmount = totalAmount.toFixed(2);
		return `已领取${receivedCount}/${totalCount}个，共${receivedAmount}/${totalAmount}元`;
}

@Component({
    filters: {
        redPacketAmount
    }
})
export default class MyTaskListComp extends Vue {

    loadingText = LOADING_TEXT;

    pageSize = 10;

    /* 滑动tab 配置 */
    activeTabIndex: string = '0';

    swiperOpt: any = new class {
        on: any = {};
        touchAngle = 20;
        iOSEdgeSwipeDetection = true;
        shortSwipes = true;
        constructor(public superThis: MyTaskListComp) {
            this.on.slideChangeTransitionEnd = function () {
                superThis.changeTab('' + this.activeIndex, false);
            };
        }
    }(this);

    /* 我的邀请函 */
    publishTasks: ITask[] = [];

    refreshingPublishTasks = false;
    
    loadingPublishTasks = false;

    publishTaskListContainer: HTMLElement = null;

    publishTaskPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    /* 我的红娘薄(我接受的任务) */
    receivedTasks: ITask[] = [];

    refreshingReceivedTasks = false;

    receivedTaskListContainer: HTMLElement = null;

    loadingReceivedTasks = false;

    receivedTaskPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    @State(state => state.user.aliPaymentId)
    alipayId: string;
    @State(state => state.user.roles)
    myRoles: string[];

    @Action('showToast')
    showToast;

    $refs: {
        myTaskSwiper: any,
        publishTaskListContainer: any,
        receivedTaskListContainer: any
    };

    get swiper() {
        return this.$refs.myTaskSwiper.swiper;
    }

    async mounted() {
        console.log('mounted');
        this.publishTaskListContainer = this.$refs.publishTaskListContainer;
        this.receivedTaskListContainer = this.$refs.receivedTaskListContainer;
    }

    activated() {
          // 刷新我的邀请函
          this.refreshPublishTasks();
          // 刷新我的红娘薄
          this.refreshReceivedTasks();
    }
    
    // deactivated() {
        
    // }

    changeTab(val: string, isSlide: boolean = true) {
        this.activeTabIndex = val;
        if (isSlide) {
            this.swiper.slideTo(parseInt(val, 10));
        }
    }

    /**
     * 刷新我的邀请函
     */
    async refreshPublishTasks() {
        // console.log('刷新我的邀请函');
        this.refreshingPublishTasks = true;
        try {
            const res = await Api.getPublishedTasks(1, this.pageSize);
            this.publishTasks = res.body.obj.rows;

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.publishTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingPublishTasks = false;
        }
    }

    /**
     * 加载我的邀请函
     */
    async loadMorePublishTasks() {
        // console.log('加载更多');
        this.loadingPublishTasks = true;
        try {
            const res = await Api.getPublishedTasks(this.publishTaskPage.curPage + 1, this.pageSize);
            const publishTasks: ITask[] = res.body.obj.rows;
            this.publishTasks = this.publishTasks.concat(publishTasks);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.publishTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.publishTaskPage.hasMore = false;
        } finally {
            this.loadingPublishTasks = false;
        }
    }

    /**
     * 刷新我的红娘薄
     */
    async refreshReceivedTasks() {
        this.refreshingReceivedTasks = true;
        try {
            const res = await Api.getReceivedTasksHistory(1, this.pageSize);
            this.receivedTasks = res.body.obj.rows;

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.receivedTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingReceivedTasks = false;
        }
    }

    /**
     * 加载更多接受的任务
     */
    async loadMoreReceivedTasks() {
        this.loadingReceivedTasks = true;
        try {
            const res = await Api.getReceivedTasksHistory(this.receivedTaskPage.curPage + 1, this.pageSize);
            const tasks: ITask[] = res.body.obj.rows;
            this.receivedTasks = this.receivedTasks.concat(tasks);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.receivedTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.receivedTaskPage.hasMore = false;
        } finally {
            this.loadingReceivedTasks = false;
        }
    }

    /**
     * 进入推荐详情页面（接受任务的详情）
     * @param task 
     */
    toRecommendDetailPage(task: ITask) {
        const params: IReceivedTaskDetailParams = {
            taskId: task.taskId
        };
        this['$router'].push({
            name: 'receivedTaskDetail',
            params
        });
    }

    /**
     * 进入推荐页面
     * @param event 
     * @param task 
     */
    toRecommendPage(event: Event, task: ITask) {
        // 判断任务是否正在进行
        if (task.subsetTasks[0].taskStatus === 0) {
            const params: IRecommendParams = {
                action: 'task',
                uid: task.fromUid,
                subId: task.subsetTasks[0].subId
            };
            this['$router'].push({
                name: 'recommend',
                params
            });
        }
        event.stopPropagation();
    }

    /**
     * 领取任务金额
     * @param event 
     * @param task 
     */
    async clearTask(event: Event, task: ITask) {
        const clearingStatus = task.subsetTasks[0].clearingStatus;
        if (clearingStatus === 0 || clearingStatus === -1) { // 金额未结算 或者 结算失败
            if (this.alipayId === null) {
                this.showToast(new ToastMsg('您未绑定支付宝账户，请往绑定支付宝账户。'));            
                this['$router'].push({
                    name: 'bindAlipay'
                });
                return;
            }
            if (this.myRoles.indexOf(USER_ROLES.authorized_matchmaker) === -1) {
                this.showToast(new ToastMsg('您未认证，暂时无法领取红包，请先完成认证。'));
                // 跳转到验证界面，待增加
                this['$router'].push({
                    name: 'authIndex'
                });
                return;
            }
            try {
                const res = await Api.clearTask(task.subsetTasks[0].subId, 1);
                this.showToast(new ToastMsg('申请结算成功，红包将在24小时内发放至您的支付宝账户！'));
            } catch (e) {
                this.showToast(new ToastMsg(e.message));
            }            
        } else if (clearingStatus === 1) {
            this.showToast(new ToastMsg('金额结算中'));
        } else {
            this.showToast(new ToastMsg('金额已结算'));
        }
        event.stopPropagation();
    }

    /**
     * 点击我发布的任务
     * @param task 
     */
    clickPublishTask(task: ITask) {
        if (task.paymentStatus === 0) {
            // 未支付跳转到支付页面
            // Utils.saveItem(CACHE_NAME.payTask + task.taskId, task);
            const params: IPayParams = {
                taskId: task.taskId,
                action: 'fromList'
            };

            Utils.saveItem(CACHE_NAME.payParams, params);

            this['$router'].push({
                name: 'pay'
                // params
            });
        } else {
            const params: IPublishedTaskParams = {
                taskId: task.taskId
            };
            this['$router'].push({
                name: 'publishTaskDetail',
                params
            });
            // $state.go('orderDetail', {
            //     taskId: order.taskId
            // });
        }
    }
}



